import React from 'react';
import { useLottie, LottieConfig, Lottie } from 'react-lottie-hook';
import styled from 'styled-components';

import splendidAnimationData from './splendid-animation.json';

const StyledContainer = styled.div``;

const StyledSpinner = styled(Lottie)`
    position: relative;
`;

const StyledTitle = styled.div`
    margin-top: 16px;

    font-weight: 500;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -0.019em;

    text-align: center;

    color: #ffffff;
`;

const lottieOptions: LottieConfig = {
    renderer: 'svg',
    animationData: splendidAnimationData,
    loop: true,
    autoplay: true,
};

interface SplendidSpinnerProps {
    title?: string;
}

const SPLENDID_SPINNER_SIZE_PX = 280;

export const SplendidSpinner: React.FC<SplendidSpinnerProps> = ({ title }) => {
    const [lottieRef] = useLottie(lottieOptions);

    return (
        <StyledContainer>
            <StyledSpinner lottieRef={lottieRef} width={SPLENDID_SPINNER_SIZE_PX} height={SPLENDID_SPINNER_SIZE_PX} />
            {title && <StyledTitle>{title}</StyledTitle>}
        </StyledContainer>
    );
};
