import React from 'react';
import styled, { css } from 'styled-components';
import { useFocusOnMount } from 'hooks';

const StyledTitle = styled.div`
    margin-top: 12px;
    color: rgba(255, 255, 255, 0.28);
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
`;

const StyledInput = styled.input`
    margin-top: 6px;
    height: 40px;
    width: 100%;
    font-weight: 500;
    font-size: 32px;
    color: #fff;
    caret-color: #2ac673;
    line-height: 40px;
    padding: 0;
    border: none;
    background: transparent;
`;

interface StyledRootProps {
    isFocused?: boolean;
}
const StyledRoot = styled.div<StyledRootProps>`
    height: 112px;
    width: 900px;
    box-sizing: border-box;
    padding: 0 32px;

    border-top: 4px solid #1a1a1a;
    border-left: 4px solid #1a1a1a;
    border-right: 4px solid #1a1a1a;
    border-bottom: 4px solid rgba(255, 255, 255, 0.12);

    border-radius: 24px;
    background-color: #1a1a1a;

    transition: border 0.2s ease-in-out;

    ${({ isFocused }) =>
        isFocused &&
        css`
            border-width: 4px;
            border-style: solid;
            border-color: #2ac673;

            ${StyledTitle} {
                color: #2ac673;

                transition: color 0.2s ease-in-out;
            }
        `}
`;

interface InputProps extends React.InputHTMLAttributes<HTMLInputElement> {
    title: string;
    onSubmit?: (event: React.SyntheticEvent<HTMLInputElement>) => void;
    shouldFocusOnMount?: boolean;
}

export const Input: React.FC<InputProps> = ({ title, shouldFocusOnMount, onSubmit, ...props }) => {
    const [isFocused, setFocus] = React.useState(false);
    const inputRef = React.useRef<HTMLInputElement>(null);
    const onMountRef = useFocusOnMount<HTMLDivElement>();
    const memoizedOnRootFocus = React.useCallback(() => setFocus(true), [setFocus]);
    const memoizedOnRootBlur = React.useCallback(() => setFocus(false), [setFocus]);
    const memoizedOnInputKeyPress = React.useCallback(
        (event: React.KeyboardEvent<HTMLInputElement>) => {
            if (onSubmit && event && event.key === 'Enter') {
                onSubmit(event);
            }
        },
        [onSubmit],
    );
    const memoizedOnRootKeyPress = React.useCallback(() => {
        if (inputRef && inputRef.current) {
            inputRef.current.focus();
        }
    }, []);

    return (
        <StyledRoot
            tabIndex={0}
            ref={shouldFocusOnMount ? onMountRef : undefined}
            isFocused={isFocused}
            onFocus={memoizedOnRootFocus}
            onBlur={memoizedOnRootBlur}
            onKeyPress={memoizedOnRootKeyPress}
        >
            <StyledTitle>{title}</StyledTitle>
            <StyledInput tabIndex={-1} ref={inputRef} type="text" onKeyPress={memoizedOnInputKeyPress} {...props} />
        </StyledRoot>
    );
};
