import { DGBuilding } from 'types/2gis-maps';

import { Reducer, initialState, AppState } from '../../constants';

const updateUserAddressBasedOnBuilding = ({ user }: AppState, building: DGBuilding) => {
    // eslint-disable-next-line @typescript-eslint/camelcase
    const { point, address, address_name, full_name } = building;
    const [addressComponent] = address.components;
    const { street, number } = addressComponent;
    // eslint-disable-next-line @typescript-eslint/camelcase
    const [city] = full_name.split(', ');

    user.address.city = city;
    user.address.street = street;
    user.address.house = number;
    // eslint-disable-next-line @typescript-eslint/camelcase
    user.address.address_string = address_name;
    user.address.location.latitude = String(point.lat);
    user.address.location.longitude = String(point.lon);
};

export const userData: Reducer = (state, action) => {
    const { addressSimplePage, user } = state;

    switch (action.type) {
        case 'UPDATE_USER': {
            state.user = { ...state.user, ...action.payload };

            break;
        }

        case 'GO_TO_CATALOG': {
            state.user = action.payload.address;

            break;
        }

        case 'address/CHANGE_INPUT_VALUE': {
            user.address = { ...initialState.user.address };

            break;
        }

        case 'address/CLICK_SUGGEST_ITEM': {
            const suggestItem = addressSimplePage.suggestsRaw[action.payload];

            if (suggestItem.type !== 'building') {
                break;
            }

            updateUserAddressBasedOnBuilding(state, suggestItem);

            break;
        }

        default:
            break;
    }
};
